@use "variables" as v;

//Mixins for mediaQueries
@mixin phone{
    @media (min-width: v.$phone){
        @content;
    }
}
@mixin tablet{
    @media (min-width: v.$tablet){

        @content;
    }
}
@mixin desktop{
    @media (min-width: v.$desktop){
        @content;
    }
}

//General Mixins
@mixin grid($columns, $rows, $gap){
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    grid-template-rows: repeat($rows, 1fr);
    gap: $gap;
}

@mixin button($bg, $bg-hovers, $txt, $txt-hover){
    background-color: $bg;
    color: $txt;
    transition: v.$tran-btn;
    padding: 1rem 1rem;
    font-weight: 500;
    text-decoration: none;
    //text-transform: uppercase;
    font-size: 17px;
    letter-spacing: 0.1rem;
    border: none;
    border-radius: 1rem;
    margin-top: 2rem;
    width: 100%;
    height: 50px;
    padding: 1rem 4rem;
    @include tablet{
        width: 50%;
        font-size: 20px;
    }
    &:hover{
        background-color: $bg-hovers;
        color: $txt-hover;
        transition: v.$tran-btn;
    }
}

@mixin input($bg, $border, $border-color, $disabled-bg) {
    background-color: $bg;
    border: 1.5px solid $border;
    border-radius: 10px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
    transition: 0.2s ease-in-out;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 1.4rem;
    width: 100%;
    .view-icon{
        cursor: pointer;
        margin-right: 2rem;
    }

    &:focus {
        outline: none;
    }

    &:focus-within {
        border: 1.5px solid $border-color;
    }

    &:disabled {
        background-color: $disabled-bg;
        border: 1.5px solid $border-color;
    }

    @include tablet{
        font-size: 1.6rem;
    }

}
