@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.profileHeroSA {
    
    text-align: left;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    gap: 2rem;
    padding: 2rem;
    background-color: v.$blanco;
    width: 90%;
    border-radius: 2%;
    box-shadow: 10px 10px 25px -4px rgba(139, 139, 139, 0.75);

    @include m.tablet {
        width: 500px;
        margin-bottom: 9rem;
        flex-direction: column;
    }

    &__left {
        text-align: left;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 10rem;

        &__titulo {
            color: v.$negro;
            font: v.$titulos;
            margin: 0;
            font-size: v.$titulos-size;
            //top: 2rem;
            text-align: center;
        }
    }

    &__right {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        min-height: 10rem;
        width: 90%;

        @include m.phone {
            width: 100%;
        }

        &__subtitulo {
            font-size: 1.8rem;
            color: v.$negro;
            font-weight: bold;
            margin-top: 2rem;
        }

        &__contenido {
            font-size: 1.6rem;
            color: v.$gris;
            margin-left: 2rem;
        }

        &__inputProfile {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;

            &__label {
                font-size: 1.8rem;
            }

            &__content {
                border: 1.5px solid #ecedec;
                border-radius: 10px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: 10px;
                transition: 0.2s ease-in-out;
                margin-bottom: 2rem;
                font-size: 1.4rem;
                width: 100%;

                .inputSA {
                    margin-left: 10px;
                    border-radius: 10px;
                    border: none;
                    width: 90%;
                    height: 100%;

                    &:focus {
                        outline: none;
                        border-color: v.$secundario;
                    }
                }

            }
        }

        //botones de guardar y cancelar
        &__buttonContainer {
          width: 100%;
          &__button {
            @include m.tablet{
              display: flex;
              justify-content: space-between;
              gap: 2rem;
              max-width: 50rem;
            }
            &__primario {
                @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
            }

            &__secundario {
                @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
            }
          }

          &__buttonEdit {
            @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
          }
        }
    }

    .view {
        justify-content: center;
    }
}