@use 'Base/variables' as v;
@use 'Base/mixins' as m;

    .container-shadow {
        padding: 20px;
        background-color: #ffffff;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        margin: 20px;
        height: auto;
        overflow-y: auto;

        @media (min-width: 768px) {
            max-width: calc(100% - 40px);
            margin-top: 20px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    .editForm {
        text-align: left;
        background-color: v.$blanco;

        @include m.tablet {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 90%;
            text-align: left;
        }

        .editForm__titulo {
            color: v.$negro;
            font: v.$titulos;
            margin: 0;
            font-size: 2.7rem;
            //top: 2rem;
            text-align: center;

            @include m.tablet {
                font-size: v.$titulos-size;
                max-width: calc(100% - 40px);

            }
        }

        .editForm__label {
            font-size: 1.6rem;
            margin-bottom: 5px;

            @include m.tablet {
                font-size: 2rem;
                max-width: calc(100% - 40px);

            }
        }

        .inputForm {
            .input {
                @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
            }
        }

        .button-container {

            @include m.tablet {
                display: flex;
                gap: 2rem;
                max-width: 50rem;
            }

            .button {
                @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
            }
            
            .button-submit {
                @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
            }
        }
    }