@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.recuperarQR {
    padding: 0;

    .title-example {
        margin-top: 0;
        margin-bottom: 3rem;
    }

    .__Tabla {
        width: fit-content;
        max-width: 100%;
        margin: 0 auto;

        .tabla {
            .tabla__cabecera {
                tr {
                    #fecha {
                        width: 23rem;
                    }

                    .cabecera {
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                        background-color: v.$tabla-cabecera;
                        color: v.$tabla-cabecera-txt;
                    }

                    .cabecera:hover {
                        background-color: v.$tabla-cabecera-hover;
                    }
                }
            }

            .tabla__cuerpo {
                tr {

                    .id,
                    .reunion,
                    .fecha,
                    .hora,
                    .nombreAnfi {
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                    }

                    .acciones {
                        text-align: center;
                        padding: 0.5rem;
                    }
                }
            }

            .tabla__cabecera,
            .tabla__cuerpo {
                tr {
                    .acciones {
                        text-align: center;
                        vertical-align: middle;
                        padding: 0rem;
                    }
                }
            }
        }
    }
}