@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.crearReunion {
    background-color: v.$blanco;
    padding: 4rem;
    box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);

    @include m.tablet {
        max-width: 80rem;
        margin: 0 auto;
    }

    .div__reuniones {
        border-top: 2px solid v.$gris;
        border-bottom: 2px solid v.$gris;
        min-height: 0;
        margin-top: 2rem;
        margin-bottom: 3rem;
        padding: 1.5rem 0;
    }
    #AgendarReunion{
        margin-bottom: 2rem;
    }
    .Formulario {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        label{
            margin-top: 1rem;
        }
        .opCheck{
            margin-left: 3rem;
        }
        .Tabla {
            margin-top: 2rem;
            tr {
                th {
                    padding: 0.5rem;
                    text-align: center;
                    vertical-align: middle;
                }
            }
        }
    }

    .actualizarBoton {
        @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
    }

    .addFecha__boton {
        margin-bottom: 2rem;
        background-color: v.$secundario;
        color: v.$negro;
        transition: v.$tran-btn;
        padding: 0.5rem 0.5rem;
        font-weight: 500;
        text-decoration: none;
        //text-transform: uppercase;
        font-size: 13px;
        letter-spacing: 0.1rem;
        vertical-align: middle;
        border: none;
        border-radius: 1rem;
        width: 35%;
        height: 35px;
        padding: 1rem 2rem;

        @include m.tablet {
            width: 25%;
        }

        &:hover {
            background-color: v.$secundario-hover;
            color: v.$blanco;
            transition: v.$tran-btn;
        }
    }

    .button__submit {
        @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
        margin: 2rem auto;
    }

    .button__container {
        display: flex;
        justify-content: center;
        margin: 2rem 0;
    }
}