@use 'Base/variables' as v;
@use 'Base/mixins' as m;


.cSala {
    box-sizing: border-box;
    transition: margin-left 0.3s ease-in-out, width 0.3s ease-in-out;

    @media (max-width: 768px) {
        margin-left: 0;
        width: 100%;
        margin-bottom: 9rem;
    }

    .cSalaHero {
        padding: 20px;
        background-color: #ffffff;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        margin: 20px;
        height: auto;
        overflow-y: auto;

        @media (min-width: 768px) {
            max-width: calc(100% - 40px);
            margin-top: 20px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    .cSalaHeroForm {
        text-align: left;
        background-color: v.$blanco;

        @include m.tablet {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 90%;
            text-align: left;
        }

        &__container {
            &__label {
                font-size: 1.6rem;
                //margin-bottom: 5px;

                @include m.tablet {
                    font-size: 2rem;
                    max-width: calc(100% - 40px);

                }
            }

            &__input {
                @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
            }

            select {
                border: 1.5px solid #ecedec;
                border-radius: 10px;
                height: 40px;
                display: flex;
                align-items: center;
                transition: 0.2s ease-in-out;
                margin-bottom: 1rem;
                font-size: 1.4rem;
                width: 100%;
            }

            select:focus {
                outline: none;
                border-color: v.$secundario;
            }

            select option:hover {
                background-color: rgb(240, 240, 197);
                color: #000;
            }

            select option:focus {
                background-color: rgb(250, 255, 189);
                color: #000;
            }
        }

        &__button-Container {
            @include m.tablet {
                display: flex;
                justify-content: space-between;
                gap: 2rem;
                max-width: 50rem;
            }

            .Boton {
                @include m.tablet {
                    width: 50%;
                }

                .cSalaHeroForm__button-Container__button__Primario {
                    @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
                }

                .w-100 {
                    width: 100%;
                }
            }

            &__button {
                &__Secundario {
                    @include m.button(v.$primario, v.$primario-hover, v.$blanco, v.$blanco);
                }
            }
        }
    }
}