@use 'Base/variables' as v;
@use 'Base/mixins' as m;
.footer{
    background-color: v.$bg-color;
    box-shadow: 0px -8px 13px 0px rgba(0, 0, 0, 0.29);
    padding-bottom: 8rem;
    @include m.tablet{
        padding-bottom: 0;
    }
    .contenedor{
        text-align: center;
        // max-width: 60%;
        gap: 4rem !important;
        @include m.tablet{
            margin-left: 7.2%;
        }
        .beemeet{
            color: v.$primario;
            text-align: center;
            .beemeet__logo{
                width: 20rem;
                margin: 0 auto 2rem auto;
            }
        }
        .usernames{
            text-align: center;
            margin-bottom: 4rem;
            @include m.tablet{
                margin-bottom: 0;
            }
            p{
                color: v.$secundario;
                text-transform: uppercase;
                margin-bottom: 2rem;
                font-weight: bold;
            }
            .usernames__container{
                text-align: center;
                @include m.tablet{
                    display: flex;
                    gap: 4rem;
                    justify-content: center;
                    align-items: center;
                    text-align: left;
                }
                .usernames1, .usernames2{
                    p{
                        text-transform: none;
                        font-size: 1.2rem;
                        color: v.$primario;
                        margin-bottom: 1rem;
                    }
                }
            }
        }
        @include m.tablet{
            display: flex;
            gap: 8rem;
            text-align: left;
            align-items: center;
            justify-content: space-evenly;
            padding: 4rem 0;
        }
    }
    hr{
        color: v.$primario;
    }
    .copyright{
        text-align: center;
        color: v.$primario;
        padding: 2rem 0 4rem 0;
        margin-top: 2rem;
        // margin-bottom: 6rem;
        // width: 80%;
        margin-left: 7.2%;
        margin-bottom: 0;
    }
}