@use 'Base/variables' as v;
@use 'Base/mixins' as m;
.contenedorUser{
    display:block;
    align-items: center;
    justify-content: center;
    width: 100%;
        @include m.phone{
            padding-left: 8rem;
            margin-top: 0;
        }
        @include m.desktop{
            padding-left: 0;
            display: flex;
        }
    &__left{
        position: relative;
        margin: 5%;
        height: 300px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include m.tablet{
            display: flex;
            width: 50rem;
            height: 50rem;
        }

        &__circle{
            left: 50%;
            transform: translateX(-50%);
            position: relative;
            width: 300px;
            height: 300px;
            border-radius: 50%;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;

    
  
            @include m.tablet{
                left: 35%;
                width: 400px;
                height:  400px;
            }
            @include m.desktop{
                top:0;
                left: 50%;
                width: 500px;
                height: 500px;
            }

            img{      
                width: 100%;
                height: 100%;
                object-fit: cover;
                overflow: hidden;  
                display: flex;
            }

            &__icon{
                position: absolute;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                transition: all 500ms ease-out;
                    img{
                        width: 75px;
                        height: 75px;
                        display: none;
                        @include m.tablet{
                            height: 100px;
                            width: 100px;
                        }
                        @include m.desktop{
                           
                        }
                    }
                &:hover{
                    background: rgba($color: #000000, $alpha: 0.5);
                    display: flex;
                    img{
                        display: flex;
                        cursor: pointer;
                    }
                }
                //display: none;
            }

         
        }

       
    }
    
}

