@use 'Base/variables' as v;

body {
    background-color: v.$bg-color;
}

table {
    width: fit-content;
    max-width: 100%;
    tr {
        th {
            cursor: pointer;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
        }
    }
}

//Responsive
@media (min-width: 769px) {
    table {
        width: 100%;
    }
    .acciones, .accion {
        img {
            height: 2.5rem;
        }
    }
}

@media (max-width: 950px) {
    .title-example {
        font-size: 3.2rem;
    }

    table {
        font-size: 1.2rem;
        width: 100%;
    }
    
    .acciones, .accion {
        img {
            height: 2.2rem;
        }
    }
}

@media (max-width: v.$tablet) {
    .title-example {
        font-size: 2.5rem;
    }

    table {
        font-size: 1rem;
        font-weight: bold;
        width: 100%;
    }
    
    .acciones, .accion {
        img {
            height: 1.9rem;
        }
    }
}


//Hover de la tabla
.table-hover tbody tr:hover td,
.table-hover tbody tr:hover th {
    background-color: v.$table-hover;
}

//Diseo de botones
.dt-paging-button {
    padding: 0.5rem !important;
    border: none !important;
}


//Color de la cabecera de la tabla
.content {
    padding: 5rem;
    .__Tabla {
        .tabla {
            .tabla__cabecera {
                .cabecera {
                    background-color: v.$tabla-cabecera;
                    color: v.$tabla-cabecera-txt;
                }

                .cabecera:hover {
                    background-color: v.$tabla-cabecera-hover;
                }
            }

            .btn {
                display: inline-flex;

                img {
                    width: 1.8rem;
                    height: 1.8rem;
                }
            }

        }
    }
}

//Botones acciones
.acciones {
    text-align: center;
}