@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.defaultInicio{
    margin-left: 0;
    @include m.phone{

    }
    @include m.desktop{

    }
    .defaultInicioHero{
        display: flex;
        &__Top{
            display: flex;
        }
        &__Bottom{
            display: flex;
            @keyframes bee{
                0%{
                 transform: translatey(100px) scale(1, 1);
                }
                
                50%{
                 transform: translatey(0px) scale(0.8, 0.8);
                }
                
                100%{
                 transform: translatey(100px) scale(1, 1);
                }
                }
        }
    }
}