@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.consultarDatosInvitado{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    min-width: 105%;
    @include m.tablet{
        flex-direction: row;
    }
    .segundaParte{
        width: 40%;
        display: flex;
        flex-direction: column;
        align-items: end;
        justify-content: center;
        .campoTabla{
            width: 100%;
        }
        .invitadoIMG{
            width: 5rem;
            margin: 0 auto 4rem auto;
        }
    }
    .campo{
        text-align: center;
        margin-bottom: 2.5rem;
        display: flex;
        gap: 1rem;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        label{
            font-size: 2rem;
        }
        input{
            font-size: 1.4rem;
            background-color: v.$bg-color-hover;
            border: 1.5px solid #ecedec;
            border-radius: 10px;
            height: 50px;
            padding-left: 10px;
        }
        @include m.tablet{
            text-align: left;
            display: flex;
            flex-direction: row;
        }
    }
    .campoTabla{
        flex-direction: column;
        width: 100%;
        table , td, th {
            border: 1px solid #595959;
            border-collapse: collapse;
            text-align: center;
            width: 100%;
        }
        td, th {
            padding: 3px;
            width: 30px;
            height: 25px;
            font-size: 1.6rem;
            @include m.tablet{
                font-size: 1.4rem;
            }
        }
        th {
            color: v.$blanco;
            background: v.$primario;
        }
        .even {
            background: v.$primario;
        }
        .odd {
            background: v.$primario;
        }
    }
    .campoFlex{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        @include m.tablet{
            flex-direction: row;
            gap: 2rem;
        }
        .campo{
            display: flex;
            flex-direction: column;
            text-align: center;
            gap: 1rem;
            justify-content: center;
            align-items: center;
        }
    }
    .botonReturn{
        @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
        width: 100%;
        @include m.tablet{
            width: auto;
        }
    }
}