@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.catalogoSalas {
    padding: 0;
    .title-example {
        margin-top: 0;
        margin-bottom: 3rem;
    }
    .__Tabla {
        width: fit-content;
        max-width: 100%;
        margin: 0 auto;
        .tabla {
            .tabla__cabecera {
                tr {
                    #CupoMax {
                        width: 23rem;
                    }
                    .cabecera {
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                        background-color: v.$tabla-cabecera;
                        color: v.$tabla-cabecera-txt;
                    }
                    .cabecera:hover {
                        background-color: v.$tabla-cabecera-hover;
                    }
                }
            }

            .tabla__cuerpo {
                tr {

                    .id,
                    .nombreSala,
                    .estado,
                    .cupoMax,
                    .numeroS,
                    .pisoS {
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                    }
                }
            }
        }
    }
}