//Fuentes
$titulos: 'Montserrat', sans-serif;
$subtitulos: 'Roboto', sans-serif;
$texto: 'Dosis', sans-serif;

//Media Queries
$phone: 480px;
$tablet: 768px;
$desktop: 1024px;

//Colores
$primario: #305272;
$secundario: #89b6b1;
$bg-color: #fbfff1;
$blanco: #fefeff;
$negro: #0c0c0c;
$gris: #c5c5c5;
$gri: #e0e0e0;

//ColoresText
$txt-rojo:#FF3240;
$txt-azul:#090b3e;
$txt-pop:#ecfbff;

//Hover
$primario-hover: #25425e;
$secundario-hover: #678f8a;
$bg-color-hover: #e1dee6;
$blanco-hover: #e9e9e9;

//Unidades
$separacion: 5rem;
$titulos-size: 4rem;
$subtitulos-size: 3.2rem;
$texto-size: 2rem;
$boton-size: 1.5rem;

//Transiciones
$tran-btn: all 0.3s ease;
$tran-text: all 0.1s ease;
$tran-bar: all 0.5s ease;

//SideBar
$sidebar-titulos: #FFFFFF;
$arrow-bg: $secundario;
$arrow-bg-hover: $secundario-hover;

$sidebar-bg: #305272;
$sidebar-option-bg: #FFFFFF;
$color-ico-svg: #FFFFFF;
$color-ico-hover: #305272;
$color-ico-svg-active: #95beb9;
$option-text: #FFFFFF;
$option-text-hover: #111111;

//Tablas
$tabla-cabecera: #305272;
$tabla-cabecera-hover: #25425e;
$table-hover: #e5e7ec;
$tabla-cabecera-txt: #FFFFFF;

//Header
$header-bg: #fbfff1be;
$header-txt: $negro;
$header-txt-hover: $blanco;
$header-dropdown-bg: #FFFFFF;
$header-dropdown-option: $secundario;
$header-dropdown-option-txt: $negro;
$header-dropdown-item: #111111;

//Footer


