@use 'Base/variables' as v;
@use 'Base/mixins' as m;


.visualizarAgenda {
    padding: 0;
    .title-example {
        margin-top: 0;
        margin-bottom: 1rem;
    }

    .subtitle-example {
        margin-top: 0;
        margin-bottom: 3rem;
    }

    .__Tabla {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 100%;
        margin: 0 auto;

        .tabla {
            width: 100%;
            .tabla__cabecera {
                tr {
                    .cabecera {
                        padding: 1rem 2rem 1rem 2rem;
                        text-align: center;
                        vertical-align: middle;
                        background-color: v.$tabla-cabecera;
                        color: v.$tabla-cabecera-txt;
                    }

                    .cabecera:hover {
                        background-color: v.$tabla-cabecera-hover;
                    }
                }
            }

            .tabla__cuerpo {
                tr {

                    .nombreI,
                    .horario,
                    .nombreA {
                        width: fit-content;
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                    }

                    .accion {
                        width: 15%;
                        text-align: center;
                        padding: 0.5rem;
                    }
                }
            }

            .tabla__cabecera,
            .tabla__cuerpo {
                tr {
                    .accion {
                        text-align: center;
                        vertical-align: middle;
                        padding: 0rem;
                    }
                }
            }
        }
    }
}