@use 'Base/variables' as v;

.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    .cabecera{
                        width: fit-content;
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}

//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera,
            .tabla__cuerpo {
                tr {
                    .id,
                    .nombreR,
                    .nombreA,
                    .fecha,
                    .horario,
                    .sala{
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 2rem 1rem 2rem;
                    }
                }
            }
        }
    }
}

//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera,
            .tabla__cuerpo {
                tr {
                    .accion {
                        text-align: center;
                        vertical-align: middle;
                        padding: 0rem;
                    }
                }
            }
        }
    }
}

//Ajustando especficamente cabecera de Sala
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    #Sala{
                        width: 10rem;
                    }
                }
            }
        }
    }
}
