@use 'Base/variables' as v;

//Cabeceras
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    .cabecera{
                        width: fit-content;
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}

//Para centrar todo
.content {
    .__Tabla {
        .tabla {
            .tabla__cuerpo {
                tr {
                    .id,
                    .nombreAnfi,
                    .fecha,
                    .horaInicio,
                    .horaFin,
                    .sala,
                    .numInvi,
                    .numAcompa{
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 2.4rem 1rem 2.5rem;
                    }
                }
            }
        }
    }
}

//Ajustando especficamente cabecera de Cupo Mximo
.content {
    .__Tabla {
        .tabla {
            .tabla__cabecera{
                tr{
                    #Horario, #Fecha{
                        width: 17rem;
                    }
                }
            }
        }
    }
}