@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.Detalle {
    background-color: v.$blanco;
    padding: 4rem;
    box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);

    .DetalleReunion {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 2rem;
        border: none;
        padding: 1.5rem;

        @media (max-width: v.$tablet) {
            font-size: 1.5rem;
            margin-left: 3rem;
            width: inherit;
            display: contents;
            justify-content: center;
            align-items: center;
            ._idReu,
            ._tituloReu,
            ._fecha,
            ._horario {
                width: 100%;
                label {
                    margin-top: 1rem;
                }
                .guion{
                    display: none;
                }
            }
        }
        @media (max-width: v.$phone) {
            font-size: 1.2rem;
            width: inherit;
            display: flex;
            align-items: flex-start;
            flex-wrap: nowrap;
            flex-direction: column;
            ._idReu,
            ._tituloReu,
            ._fecha,
            ._horario {
                width: 100%;
            }
            label {
                margin-top: 1rem;
            }
            input{
                width: 70%;
                max-width: 100%;
                white-space: nowrap;
                overflow: auto;
            }
        }
        ._idReu,
        ._tituloReu,
        ._fecha,
        ._horario {
            width: 50%;
            label {
                margin-top: 1.5rem;
            }
        }
        ._tituloReu {
            #reunionTitulo {
                width: max-content;
                min-width: 30rem;
                max-width: 100%;
                white-space: nowrap;
                overflow: auto;
            }
        }
        ._nombreSala {
            #sala {
                width: 18rem;
                min-width: 20%;
                max-width: 100%;
                white-space: nowrap;
                overflow: auto;
            }
        }
        #salaNumero,
        #salaPiso {
            width: 5rem;
        }
        #horarioInicio {
            width: 10rem;
        }
        label {
            margin-top: 1.5rem;
            font-weight: bold;
        }
        input {
            border: none;
            background-color: v.$blanco;
        }
    }
    .__Tabla {
        @media (max-width: v.$tablet) {
            font-size: 1.4rem;
        }
        @media (max-width: v.$phone) {
            font-size: 1.1rem;
        }
        .tabla {
            .tabla__cabecera {
                tr {
                    .cabecera {
                        background-color: v.$tabla-cabecera;
                        color: v.$tabla-cabecera-txt;
                        width: fit-content;
                        padding: 1rem;
                        text-align: center;
                        vertical-align: middle;
                    }
                    .cabecera:hover {
                        background-color: v.$tabla-cabecera-hover;
                    }
                }
            }
            .tabla__cuerpo {
                tr {
                    .nombreInvi,
                    .correo {
                        text-align: center;
                        vertical-align: middle;
                        padding: 1rem 4rem 1rem 4rem;
                        @media (max-width: v.$phone) { 
                            padding: 1rem;
                        }
                    }
                }
            }
        }
    }
}