@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.registrarInforPersonal {
    background-color: v.$blanco;
    padding: 4rem;
    box-shadow: 10px 10px 8px -3px rgba(0, 0, 0, 0.33);
    .Formulario {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .hidden {
            display: none;
        }
        .informacionPersonal,
        .informacionAcompaantes,
        .informacionDispositivos,
        .informacionAutomovil {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .form-group {
                @include m.tablet {
                    max-width: 40rem;
                    input,
                    select {
                        min-width: 0;
                    }
                    select {
                        min-height: 4rem;
                    }
                }
            }
        }
        .personal__titulo,
        .acompaantes__titulo,
        .dispositivos__titulo,
        .automovil__titulo {
            margin-top: 3rem;
            margin-bottom: 3rem;
        }
        .informacionPersonal {
            .NombreCompleto {
                width: 100%;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                .form-group {
                    flex: 1;
                    min-width: 30%;
                    margin-right: 1rem;
                    &:last-child {
                        margin-right: 0;
                    }
                    @include m.tablet {
                        min-width: 25rem;
                    }
                }
            }
            .contacto, .identificacion {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                .form-group {
                    flex: 1;
                    min-width: 48%;
                    margin-right: 1rem;

                    &:last-child {
                        margin-right: 0;
                    }

                    @include m.tablet {
                        min-width: 45%;
                    }
                }
            }
        }
        .informacionAcompaantes{
            .agregarAcompaante_boton{
                margin-bottom: 2rem;
                background-color: v.$secundario;
                color: v.$negro;
                transition: v.$tran-btn;
                padding: 0.5rem 0.5rem;
                font-weight: 500;
                text-decoration: none;
                //text-transform: uppercase;
                font-size: 13px;
                letter-spacing: 0.1rem;
                vertical-align: middle;
                border: none;
                border-radius: 1rem;
                width: 60%;
                height: 35px;
                padding: 1rem 2rem;
                @include m.tablet {
                    width: 70%;
                }
                &:hover {
                    background-color: v.$secundario-hover;
                    color: v.$blanco;
                    transition: v.$tran-btn;
                }
            }
        }
        .informacionDispositivos{
            .DatosDispositivo{
                width: 100%;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-bottom: 1rem;
                .form-group {
                    flex: 1;
                    min-width: 30%;
                    margin-right: 1rem;
                    &:last-child {
                        margin-right: 0;
                    }
                    @include m.tablet {
                        min-width: 25rem;
                    }
                }
            }
            .agregarDispositivo_boton{
                margin-bottom: 2rem;
                background-color: v.$secundario;
                color: v.$negro;
                transition: v.$tran-btn;
                padding: 0.5rem 0.5rem;
                font-weight: 500;
                text-decoration: none;
                //text-transform: uppercase;
                font-size: 13px;
                letter-spacing: 0.1rem;
                vertical-align: middle;
                border: none;
                border-radius: 1rem;
                width: 30%;
                height: 35px;
                padding: 1rem 2rem;
                @include m.tablet {
                    width: 40%;
                }
                &:hover {
                    background-color: v.$secundario-hover;
                    color: v.$blanco;
                    transition: v.$tran-btn;
                }
            }
        }
        @media (max-width: 880px) {
            .NombreCompleto, .contacto, .identificacion, .DatosDispositivo {
                flex-direction: column;

                .form-group {
                    min-width: 100%;
                    margin-right: 0;
                    margin-bottom: 1rem;

                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        }
        .form-check-input {
            min-width: 2rem;

            @include m.tablet {
                min-width: 3rem;
            }
        }
        input,
        label,
        select {
            min-width: 30rem;
            padding: 1rem;

            @include m.tablet {
                min-width: 50rem;
            }
        }
        #BotonAgregar{
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 1.5rem;
        }
        .confirm__btn {
            @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
        }
    }
}