@use "variables" as v;
@use "mixins" as m;


//Snippet Paul Irish y conversion a 1rem = 10px
html {
    box-sizing: border-box;
    font-size: 62.5%;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body{
    font-family: v.$texto;
    font-size: 1.6rem;
    background-color: v.$bg-color !important;
}
p{
    color: v.$negro;
    font-size: 2rem;
}
main.contenidoPrincipal{
    // padding: 4rem 8rem;
    width: calc(100% - 2rem);
    margin: 2rem auto;
    // display: flex;
    // justify-content: center;
    @include m.tablet{
        width: 80%;
        margin-top: 8rem;
        margin-bottom: 8rem;
        margin-right: 12.5%;
        margin-left: 12.5%;
    }
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a{
    text-decoration: none;
}
img{
    max-width: 100%;
    display: block;
    max-height: 100%;
}
h1,h2,h3{
    margin: 0 0 v.$separacion 0;
}

h1{
    font-size: 4rem;
    font-family: v.$titulos;
    padding: 0 2rem;
}
h2{
    font-size: 2.6rem;
    font-family: v.$subtitulos;
}
h3{
    font-size: 3.2rem;
}
p{
    font-family: v.$texto;
}
.msg-error-form{
    color: #d90429;
    padding: 0.8rem 2rem;
    text-align: center;
    margin: -1rem 0 1rem 0;
    border-radius: 4rem;
    border: 1px solid #d90429;
    font-size: 1.4rem;
    @include m.tablet{
        max-width: 50rem;
        padding: 0.5rem;
    }
}