@use 'Base/variables' as v;
@use 'Base/mixins' as m;

@keyframes bee{
  0%{
    transform: translatey(100px) scale(1, 1);
  }

  20%{
    transform: translatey(0px) scale(0.8, 0.8) rotate(0deg);
  }

  40%{
    transform: translatey(0px) scale(0.8, 0.8) rotate(10deg);
  }

  60%{
    transform: translatey(0px) scale(0.8, 0.8) rotate(-10deg);
  }

  80%{
    transform: translatey(0px) scale(0.8, 0.8) rotate(0deg);
  }

  100%{
    transform: translatey(100px) scale(1, 1);
  }
}

.default{
  min-height: 92vh;
}

.defaultInicioHero{
    margin-left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    &__Top{
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    &__Bottom{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 70px;
        top: 40rem;
        @include m.tablet{
          position: absolute;
          bottom: 0px;
        }
        &__imgBox{
          width: 300px;
          height: 300px;
          &__beeAnimation{
            animation: bee infinite 2s ease;
          }
          img{
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
        }
    }
}