@use 'Base/variables' as v;
@use 'Base/mixins' as m;

#header-container{
    padding: 2rem;
    @include m.tablet{
        padding: 3rem;
    }
}
#navbar-img{
    margin: 2rem;
    width: 5rem;
    height: 5rem;
    padding: 0;
    #navbar-imgs{
        width: 10rem;
        height: 10rem;
    }
}
.navbar {
    background-color: v.$header-bg;
    // border-bottom: 0.1rem solid v.$gris;
    box-shadow: 0px 8px 13px 0px rgba(0,0,0,0.29);
    @include m.tablet{
        height: 8rem;
    }
    .navbar__profile-container{
        height: 4rem;
        width: 4rem;
        margin: 2rem;
        background-image: url(../img/usuario.webp);
        background-size: 80%;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 50%;
        border: 0.2rem solid v.$primario;
    }
    .nav-link,
    .dropdown-item {
        // font-size: 2rem;
        // color: v.$header-dropdown-item;
        // background-color: v.$secundario;
        // border-radius: 2rem;
        // padding: 1rem 5rem;
        // width: 25rem;
        // text-align: center;
    }
    #navbarDropdownMenuLink{
        color: v.$header-txt;
        // font-weight: bold;
        // text-transform: uppercase;
    }
    .nav-link.dropdown-toggle {
        color: v.$header-txt; 
        // font-weight: bold;
        // text-transform: uppercase;

        &:hover{
            color: v.$header-txt-hover; 
        }
    }

    .dropdown-menu {
        background-color: v.$header-dropdown-bg;
        border-radius: 0.5rem;
        padding: 1rem;
    }

    .dropdown-item {
        color: v.$header-dropdown-item;
        padding: 1rem;
        border-radius: 0.5rem;

        &:hover,
        &:focus {
            background-color: v.$header-dropdown-option;
            color: v.$header-dropdown-option-txt;
        }
    }
    @media (max-width: 768px) {
        .nav-link,
        .navbar-brand,
        .dropdown-item {
            font-size: 1.5rem; 
        }
    }

    .navbar-brand img {
        height: 60px;
        width: 40px;
        @include m.tablet{
            display: none;
        }
    }
}

.navbar {
    background-color: v.$header-bg;
    height: 7rem;
    box-shadow: 0px 8px 13px 0px rgba(0, 0, 0, 0.29);

    @include m.tablet {
        height: 7rem;
    }

    .navbar__profile-container {
        position: relative;
        height: 4rem;
        width: 4rem;
        margin: 2rem;
        background-image: url(../img/usuario.webp);
        background-size: 80%;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 50%;
        border: 0.2rem solid v.$primario;
        cursor: pointer;
    }

    #user-menu {
        display: none;
        position: absolute;
        background-color: v.$header-dropdown-bg;
        border-radius: 1rem; 
        top: calc(100% + 10px); 
        right: 0;
        min-width: 200px; 
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
        font-family: v.$titulos; 
        font-size: 2rem;

        a {
            display: block;
            color: v.$header-dropdown-item;
            padding: 12px;
            text-decoration: none;

            &:hover {
                background-color: v.$header-dropdown-option;
                color: v.$header-dropdown-option-txt;
                border: 1px solid v.$primario;
                border-radius: 10px;
            }
        }
    }

    @media (max-width: 768px) {
        #user-menu {
            font-size: 1.5rem;
            min-width: 150px;

            a {
                padding: 10px 15px;

                &:hover {
                    background-color: v.$header-dropdown-option;
                    color: v.$header-dropdown-option-txt;
                    border: 1px solid v.$primario;
                    border-radius: 10px;
                }
            }
        }
    }
}