@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.contento {
    box-sizing: border-box;
    transition: margin-left 0.3s ease-in-out, width 0.3s ease-in-out;

    @media (max-width: 768px) {
        margin-left: 0;
        width: 100%;
        margin-bottom: 9rem;
    }

    .container-shadow {
        padding: 20px;
        background-color: #ffffff;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        border-radius: 8px;
        margin: 20px;
        height: auto;
        overflow-y: auto;

        @media (min-width: 768px) {
            max-width: calc(100% - 40px);
            margin-top: 20px;
            margin-bottom: 20px;
            //width: 90%;
        }
    }

    .consultaForm {
        text-align: left;
        background-color: v.$blanco;

        @include m.tablet {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 90%;
        }

        .consultaForm__titulo {
            color: v.$negro;
            font: v.$titulos;
            margin: 0;
            font-size: v.$titulos-size;
            //top: 2rem;
            text-align: center;

            @include m.tablet {
                font-size: 4rem;
            }
        }

        .consultaForm__label {
            font-size: 1.6rem;
            margin-bottom: 5px;

            @include m.tablet {
                font-size: 2rem;
            }
        }

        .consultaForm__input {
            @include m.input(v.$blanco, #ecedec, v.$primario, v.$gri);
        }

        .button-container {
            .button {
                @include m.button(v.$secundario, v.$secundario-hover, v.$negro, v.$blanco);
            }

            @include m.tablet {
                display: flex;
                gap: 2rem;
                max-width: 50rem;
            }
        }
    }

}